/* ------------------------ Conversation tab ------------------------ */
.fa-2x {
  font-size: 2em;
}

.heading {
  padding: 0;
  margin: 0 0 5px 0;
  height: 55px;
  width: 100%;
  background-color: f7f7f7;
  z-index: 1000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0 1px 2px rgb(206, 206, 206);
}

.heading-avatar-icon #img-mess {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  margin-top: 10px;
}

.heading-avatar-icon #img-header {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  margin-top: 10px;
  margin-left: 60%;
}

.heading-name {
  padding: 0 !important;
  cursor: pointer;
  height: 100%;
  margin-left: 10px;
}

.heading-name-meta {
  font-weight: 700;
  font-size: 100%;
  margin-top: 2px;
  padding: 5px;
  padding-bottom: 0;
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #000;
  display: block;
}

.heading-online {
  color: rgb(43, 140, 66);
  padding: 0 5px;
  font-size: 12px;
}



.heading-dots i {
  text-align: right;
  padding: 5px;
  color: #93918f;
  cursor: pointer;
}
.dropdown-item:active
{
  background-color: #d0d6e2;
  color: #000;
}
.conversation-tab {
  margin: 5px 0px 0px 0px;
  border: solid 1px black;
  height: 550px;
  width: 100%;
  border-radius: 10px;
  border: solid 1px rgb(206, 206, 206);
  box-shadow: rgb(206, 206, 206) 5px 5px;
  border-radius: 10px;
  background-color: white;
}

.conversation {
  padding: 0 !important;
  margin: 0 !important;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.message {
  padding: 0 !important;
  margin: 0 !important;
  background-size: cover;
  height: calc(100% - 120px);
  overflow-y: auto;
  box-shadow: rgb(206, 206, 206) 0 1px;
}

.message-previous {
  margin: 0 !important;
  padding: 0 !important;
  height: auto;
  width: 100%;
}

.previous {
  font-size: 15px;
  text-align: center;
  padding: 10px !important;
  cursor: pointer;
}

.previous a {
  text-decoration: none;
  font-weight: 700;
}

.message-body {
  margin: 0 !important;
  padding: 0 !important;
  width: 100%;
  height: auto;
}

.message-main-receiver {
  padding: 3px 20px !important;
  max-width: 60%;
}

.message-main-sender {
  padding: 3px 20px !important;
  margin-left: 40% !important;
  max-width: 60%;
  padding-bottom: 0;
  margin-bottom: 0;
}

.message-text {
  margin: 0 !important;
  padding: 5px !important;
  word-wrap: break-word;
  font-size: 14px;
  padding-bottom: 0 !important;
}

.message-time {
  margin: 0 !important;
  margin-left: 50px !important;
  font-size: 12px;
  text-align: right;
  color: #9a9a9a;
  float: right;
}

.receiver {
  width: auto !important;
  padding: 4px 10px 7px !important;
  margin-left: 10px;
  border-radius: 10px 10px 10px 0;
  background: rgb(228, 230, 235);
  font-size: 12px;
  text-shadow: 0 1px 1px rgba(0, 0, 0, .2);
  word-wrap: break-word;
  display: inline-block;
}

.receiver .message-text,
.receiver .message-time {
  color: black;
}

.sender {
  float: right;
  width: auto !important;
  background: rgb(66, 133, 244);
  border-radius: 10px 10px 0 10px;
  padding: 4px 10px 7px !important;
  font-size: 12px;
  text-shadow: 0 1px 1px rgba(0, 0, 0, .2);
  word-wrap: break-word;
}

.sender .message-text,
.sender .message-time {
  color: white;
}


/*Reply*/

.reply {
  height: 60px;
  width: 100%;
  background-color: f7f7f7;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 10px 5px 10px 5px !important;
  margin: 0 !important;
  z-index: 1000;
}

.reply div {
  text-align: center;
}

.reply i {
  color: #93918f;
  font-size: 25px;
}

.reply i:hover {
  color: rgb(66, 133, 244);
  font-size: 25px;
}

.reply-emojis,
.reply-recording,
.reply-send {
  padding: 5px !important;
}

.reply-main {
  padding: 2px 5px !important;
  background-color: white;
}

.reply-main textarea {
  width: 100%;
  resize: none;
  overflow: hidden;
  padding: 5px !important;
  outline: none;
  border: none;
  text-indent: 5px;
  box-shadow: none;
  height: 100%;
  font-size: 16px;
}

.reply-main textarea:focus {
  outline: none;
  border: none;
  text-indent: 5px;
  box-shadow: none;
}

.list-emoji {
  transform: translateY(-320px);
  width: 300px;
  position:relative;
  z-index: 999;
}

.list-emoji span {
  border: none;
  margin: 0px;
  padding: 0px;
}

.list-emoji section:first-child {
  box-shadow: 5px 2px 8px 2px #888888;
}

.emoji-mart {
  width: 100% !important;
  height: 100% !important;
}

.emoji-mart .emoji-mart-bar:last-child, .emoji-mart .emoji-mart-search {
  visibility: hidden;
  display: none;
}

